@import 'variables.scss';

body {
    font-family: $p-font;
    font-weight: $f-weight;
    color: $text-color;
    font-size: $p-desctop-size;
    @media screen and (max-width: $screen-tablet) {
        font-size: $p-1000px-size;
    }
    @media screen and (max-width: $screen-phone) {
        font-size: $p-480px-size;
    }
}

a {
    color: $text-color;
    cursor: pointer;
    text-decoration-color: rgba(0, 0, 0, 0);
    transition: .2s;
}

a:hover {
    text-decoration: underline;
    transition: .2s;
}

h1 {
    font-family: $h1-desctop-font;
    font-size: $h1-desctop-size;
    @media screen and (max-width: $screen-tablet) {
        font-family: $h1-1000px-font;
        font-size: $h1-1000px-size;
    }
    @media screen and (max-width: $screen-phone) {
        font-family: $h1-480px-font;
        font-size: $h1-480px-size;
    }
}

h2 {
    font-family: $h2-desctop-font;
    font-size: $h2-desctop-size;
    @media screen and (max-width: $screen-tablet) {
        font-family: $h2-1000px-font;
        font-size: $h2-1000px-size;
    }
    @media screen and (max-width: $screen-phone) {
        font-family: $h2-480px-font;
        font-size: $h2-480px-size;
    }
}

h3 {
    font-family: $h3-desctop-font;
    font-size: $h3-desctop-size;
    @media screen and (max-width: $screen-tablet) {
        font-family: $h3-1000px-font;
        font-size: $h3-1000px-size;
    }
    @media screen and (max-width: $screen-phone) {
        font-family: $h3-480px-font;
        font-size: $h3-480px-size;
    }
}

h4 {
    font-family: $h4-desctop-font;
    font-size: $h4-desctop-size;
    @media screen and (max-width: $screen-tablet) {
        font-family: $h4-1000px-font;
        font-size: $h4-1000px-size;
    }
}

button {
    width: 200px;
    height: 50px;
    border: 1px solid $header-text-color;
    background-color: $main-color;
    font-size: 18px;
    color: $header-text-color;
    transition: .2s;
    @media screen and (max-width: $screen-tablet) {
        width: auto;
        height: auto;
    }
}

button:hover {
    background-color: $hover-color;
}

button:active {
    background-color: $active-color;
}

.text-acccent {
    color: $main-color !important;
}