@use "sass:math";

$font-federo: 'Federo', sans-serif;
$font-century-gothic: 'Century Gothic', sans-serif;

$h1-desctop-font: $font-federo;
$h1-1000px-font: $font-federo;
$h1-480px-font: $font-federo;
$h1-desctop-size: 87px;
$h1-1000px-size: 75px;
$h1-480px-size: 55px;

$h2-desctop-font: $font-federo;
$h2-1000px-font: $font-federo;
$h2-480px-font: $font-federo;
$h2-desctop-size: 50px;
$h2-1000px-size: 45px;
$h2-480px-size: 35px;

$h3-desctop-font: $font-century-gothic;
$h3-1000px-font: $font-century-gothic;
$h3-480px-font: $font-federo;
$h3-desctop-size: 24px;
$h3-1000px-size: 20px;
$h3-480px-size: 26px;

$h4-desctop-font: $font-federo;
$h4-1000px-font: $font-federo;
$h4-desctop-size: 28px;
$h4-1000px-size: 30px;

$p-font: $font-century-gothic;
$f-weight: 400;
$p-desctop-size: 17px;
$p-1000px-size: 16px;
$p-480px-size: 16px;

$p-copyright-size: 12px;

$main-color: #602912;
$hover-color: #853C1C;
$active-color: #461C09;

$white-color: #FFFFFF;
$gray-background-color: #F5F5F5;
$footer-background-color: #1E1F1F;

$header-text-color: #FFFFFF;
$header-text-color-alpha: rgba(255, 255, 255, 0);
$text-color: #000000;

$container-width: 1290px;
$container-width-tablet: 1000px;
$container-width-phone: 480px;

$container-row: math.div($container-width, 12);
$container-row-tablet: math.div($container-width-tablet, 4);
$container-row-phone: math.div($container-width-phone, 4);

$screen-tablet: 1399px;
$screen-phone: 999px