@import 'zero';
@import 'variables';
@import 'basic';

.bg-body {
    &__main {
        height: calc(634px * 100vw / 1920px);
        background-color: $white-color;
        @media screen and (max-width: $screen-tablet) {
            height: calc(813px * 100vw / 1920px);
        }
        @media screen and (max-width: $screen-phone) {
            //height: calc(2008px * 100vw / 1920px);
            height: calc(100vw * 0.53371869 - 480px * 0.53371869 + 502px);
        }
    }
    &__why {
        height: calc(397px * 100vw / 1920px + 700px);
        transform: skew(0, -5deg);
        background-color: $gray-background-color;
        @media screen and (max-width: $screen-tablet) {
            height: calc(614px * 100vw / 1920px + 700px);
            transform: skew(0, -6.5deg);
        }
        @media screen and (max-width: $screen-phone) {
            // height: calc(2042px * 100vw / 1920px + 700px);
            height: 1210.5px;
            transform: skew(0, 6.5deg);
        }
    }
    &__gallery {
        height: 855px;
        margin-top: -310px;
        transform: skew(0, 1.7deg);
        background-color: $white-color;
        @media screen and (max-width: $screen-tablet) {
            height: 765px;
            transform: skew(0, 3.6deg);
        }
        @media screen and (max-width: $screen-phone) {
            height: 940px;//calc(100vw * 1.14258189 - 480px * 1.14258189 + 502px)
            height: calc(100vw * 0.94990367 - 480px * 0.94990367 + 940px);
            transform: skew(0, -4.8deg);
        }
    }
    &__how-we-work {
        height: calc(397px * 100vw / 1920px + 700px);
        transform: skew(0, -1.8deg);
        background-color: $gray-background-color;
        @media screen and (max-width: $screen-tablet) {
            height: 1533px;
            transform: skew(0, -3.3deg);
        }
        @media screen and (max-width: $screen-phone) {
        }
    }
    &__request {
        height: 855px;
        margin-top: -408px;
        transform: skew(0, 3deg);
        background-color: $white-color;
        @media screen and (max-width: $screen-tablet) {
            transform: skew(0, -4.8deg);
        }
        @media screen and (max-width: $screen-phone) {

        }
    }
}
.footer-bg-body-cleaner {
    height: 85px;
    margin-top: -642px;
    transform: skew(0, -1.3deg);
    background-color: $white-color;
    @media screen and (max-width: $screen-tablet) {
        transform: skew(0, -2deg);
    }
}


.new-body {
    position: absolute;
    top: 100px;
    width: 100%;
}

.header {
    display: flex;
    background-color: $main-color;
    height: 100px;
    color: $header-text-color;

    // @media screen and (max-width: $screen-phone) {
    //     width: $container-width-phone;
    //     margin: 0 auto;
    // }
    
    a {
        color: $header-text-color;
        text-decoration-color: $header-text-color-alpha;
    }
    a:hover {
        text-decoration-color: $header-text-color;
    }

    ul {
        display: flex;
        align-content: center;
        margin: auto;
        justify-self: center;
        max-height: fit-content;
    }

    li {
        margin: 12.5px;
        font-size: 18px;
        display: flex;
    }

    &__wrapper {
        display: flex;
        width: $container-width;
        margin: 0 auto;
        justify-content: space-between;
        align-content: center;
        @media screen and (max-width: $screen-tablet) {
            width: $container-width-tablet;
        }
        @media screen and (max-width: $screen-phone) {
            min-width: 181px;
            font-size: 22px;
            flex-direction: row-reverse;
        }
    };
    &__menu {
        display: flex;
        padding-right: 10px;
        width: $container-row * 5;
        height: auto;
        margin: auto;
        @media screen and (max-width: $screen-tablet) {
            width: $container-row-tablet * 2;
            padding-left: 30px;
            padding-right: 0;
        }
        @media screen and (max-width: $screen-phone) {
            display: none;
        }
    };
    &__logo.logo {
        display: flex;
        margin: auto;
        flex-direction: column;
        width: $container-row * 2;
        text-decoration: none !important;
        @media screen and (max-width: $screen-tablet) {
            width: $container-row-tablet * 1;
        }
        @media screen and (max-width: $screen-phone) {
            width: auto;
            @media screen and (max-width: 642px) {
                margin-left: 10px;
            }
            @media screen and (min-width: 643px) {
                padding-right: 170px !important;
                width: auto;
            }
        }
    };
    .logo {
        padding-right: 8px;
        &__image {
            width: 55px;
            height: 51px;
            margin: 0 auto 9px;
            background-image: url('../img/svg/logo.svg');
            @media screen and (max-width: $screen-phone) {
                width: 43px;
                height: 41.5px;
                background-size: 43px 41.5px;
                margin: -2px auto 7px;
                // margin: -2px 0 7px 17px;
            }
        }
        &__text {
            margin: 0 auto;
            font-family: $font-century-gothic;
            font-size: 10px;
            letter-spacing: 0.325em;
            text-transform: uppercase;
            @media screen and (max-width: $screen-phone) {
                font-size: 8px;
            }
        }
        @media screen and (max-width: $screen-phone) {
        }
    }
    &__right {
        display: flex;
        justify-content: flex-end;
        width: $container-row * 5;
        @media screen and (max-width: $screen-tablet) {
            width: $container-row-tablet * 1 + 20px;
        }
        @media screen and (max-width: $screen-phone) {
            width: auto;
        }
    }
    &__phone.phone {
        display: flex;
        margin: auto 0;
        justify-content: space-evenly;
    }
    .phone {
        display: flex;
        margin-right: 70px;
        align-content: center;
        &__icon {
            width: 30px;
            height: 30px;
            background-image: url('../img/svg/phone_header.svg');
            @media screen and (max-width: $screen-phone) {
                margin: 4px 4px 0;
                margin-top: 4px;
                margin-left: 4px;
                width: 24px;
                height: 24px;
                background-image: url('../img/svg/phone_header.svg');
                background-size: 100%;
            }
        }
        &__text {
            margin-left: 10px;
            font-size: 25px;
            color: $header-text-color;
            @media screen and (max-width: $screen-phone) {
                margin-left: 2px;
                font-size: 22px;
                padding-top: 2px;
            }
        }
        @media screen and (max-width: $screen-tablet) {
            margin-right: 50px;
        }
        @media screen and (max-width: $screen-phone) {
            margin-left: 30px;
        }
    }
    &__order-button {
        margin: auto 0;
        @media screen and (max-width: $screen-tablet) {
            display: none;
        }
    }
    &__burger-menu {
        display: none;
        height: 100%;
        width: 28px;
        height: 22px;
        margin: 38px;
        background: url('../img/svg/menu.svg') no-repeat center;
        @media screen and (max-width: $screen-phone) {
            display: flex;
        }
    }
}

.container {
    width: $container-width;
    margin: 0 auto;
    @media screen and (max-width: $screen-tablet) {
        width: $container-width-tablet;
    }
    @media screen and (max-width: $screen-phone) {
        width: $container-width-phone;
    }
}

.main {
    background: url(../img/closet.png) no-repeat right top;
    background-size: 48.5% auto;
    @media screen and (max-width: $screen-tablet) {
        background: url(../img/closet_1000.png) no-repeat right top;
        background-size: 44.9% auto;
    }
    @media screen and (max-width: $screen-phone) {
        background: url(../img/closet_480.png) no-repeat right top;
        background-size: 48% auto;
        background-position-y: 73px;
    }
}

.section-main {
    &__up-title {
        padding-top: 110px;
        margin: 0 145px -15px;
        @media screen and (max-width: $screen-tablet) {
            padding-top: 77px;
            margin: 0 125px -15px;
        }
        @media screen and (max-width: $screen-phone) {
            padding-top: 40px;
            margin: 0 0 -5px 98px;
        }
    }
    &__down-title {
        @media screen and (max-width: $screen-tablet) {
            margin-left: 12px;
        }
        @media screen and (max-width: $screen-phone) {
            margin-left: 25px;
        }
    }
    &__description {
        margin: 89px 0 95px;
        max-width: 588px;
        line-height: 130%;
        @media screen and (max-width: $screen-tablet) {
            margin: 50.5px 21px 75.5px;
            max-width: 488px;
        }
        @media screen and (max-width: $screen-phone) {
            font-family: 'Century Gothic';
            margin: calc(100vw * 0.26974952 - 480px * 0.26974952 + 44px) 18px calc(100vw * 0.26974952 - 480px * 0.26974952 + 49.5px);
            max-width: 210px;
            font-size: 16px;
            span {
                display: block;
                margin-top: 15px;
            }
        }
    }
    &__buttons {
        display: flex;
        @media screen and (max-width: $screen-tablet) {
            height: 51px;
        }
        @media screen and (max-width: $screen-phone) {
            flex-direction: column;
            height: auto;
        }
    }
}
.main-buttons {
    @media screen and (max-width: $screen-tablet) {
        margin-left: 20px;
    }
    @media screen and (max-width: $screen-phone) {
        margin-left: 18px;
    }
    &__free {
        width: 245px;
        height: 60px;
        margin-right: 20.5px;
        border: 2px solid $main-color;
        @media screen and (max-width: $screen-tablet) {
            width: auto;
            height: auto;
            font-size: 16px;
            padding: 0 38px;
        }
        @media screen and (max-width: $screen-phone) {
            width: 213px;
            height: 51px;
            font-size: 16px;
            padding: 0 auto;
        }
    }
    &__watch {
        width: 280px;
        height: 60px;
        border: 2px solid $main-color;
        background-color: white;
        color: $main-color;
        @media screen and (max-width: $screen-tablet) {
            width: auto;
            height: auto;
            font-size: 16px;
            padding: 0 28.5px;
        }
        @media screen and (max-width: $screen-phone) {
            margin-top: 10px;
            width: 242px;
            height: 51px;
            font-size: 16px;
            padding: 0 auto;
        }
    }
}

.section {
    display: flex;
    flex-direction: column;
}
.section-why {
    z-index: -1;
    &__title {
        margin: calc(100vw * 0.5076923 - 1400px * 0.5076923) 0 62px;
        &::after {
            content:"\A";
            width: 95px;
            border-bottom: 2px solid $main-color;
            height: 1px;
            margin: -1px -25px;
            display: flex;
        }
        @media screen and (max-width: $screen-tablet) {
            margin: calc(100vw * 0.55137845 - 1000px * 0.55137845 + 121px) 0 62px 50px;
            &::after {
                width: 90px;
                margin: -3px -30px;
            }
        }
        @media screen and (max-width: $screen-phone) {
            // margin: calc(100vw * 0.55137845 - 1000px * 0.55137845 + 121px) 0 62px 50px;
            margin: 143px 0 57px 20px;
            &::after {
                width: 69px;
                margin: -2px -22px;
            }
        }
    }
    &__subtitle {
        color: $main-color;
        @media screen and (max-width: $screen-tablet) {
            max-width: 320px;
            line-height: 130%;
        }
        @media screen and (max-width: $screen-phone) {
            font-size: 26px;
            max-width: 320px;
            line-height: 130%;
            line-height: 100%;
        }
    }
    &__text {
        min-width: 529px;
        margin: 29px 131px 59px 0;
        padding-left: 8.5px;
        border-left: 2px solid #602912;
        line-height: 130%;

        p.first-parag_margin {
            margin-bottom: 16px;
        }

        @media screen and (max-width: $screen-tablet) {
            width: 409.5px;
            max-width: 409.5px;
            min-width: 409.5px;
            margin: 25px 81px 40px 0;
            padding-left: 8.5px;
        }
        @media screen and (max-width: $screen-phone) {
            margin: 11px 81px 34px 0;
        }
    }
    
}
.cols{
    display: flex;
    @media screen and (max-width: $screen-tablet) {
        margin-left: 20px;
    }
    @media screen and (max-width: $screen-phone) {
        display: none;
    }
}
.cols-mobile {
    display: none;
    flex-direction: column;
    margin-left: 19px;
    @media screen and (max-width: $screen-phone) {
        display: flex;
    }
}

.section-gallery {
    margin-top: calc(100vw * 0.06923077 - 1400px * 0.06923077 + 128px);
    @media screen and (max-width: $screen-tablet) {
        //margin-top: calc(100vw * 0.06923077 - 1400px * 0.06923077 + 128px);
        margin-top: calc(100vw * 0.15037594 - 1000px * 0.15037594 + 81px);
    }
    &__title {
        margin-bottom: 58px;
        &::after {
            content:"\A";
            width: 234px;
            border-bottom: 2px solid $main-color;
            height: 1px;
            margin: -1px -25px;
            display: flex;
        }
        @media screen and (max-width: $screen-tablet) {
            margin-left: 50px;
            margin-bottom: 63.5px;
            &::after {
                width: 218px;
                margin: -2px -30px;
                display: flex;
            }
        }
        @media screen and (max-width: $screen-phone) {
            text-align: end;
            margin: 133px 20px;
            &::after {
                display: flex;
                content:"\A";
                width: 152px;
                border-bottom: 2px solid $main-color;
                height: 1px;
                margin: -2px 195px;
                display: flex;
                margin-left: auto;
            }
            margin-left: 50px;
            margin-bottom: 47px;
        }
    }
    @media screen and (max-width: $screen-phone) {
        display: none;
    }
}
.gallery {
    display: flex;
    flex-direction: row;

    &__left-block {
        display: flex;
        flex-direction: column;
        width: 960px;
        @media screen and (max-width: $screen-tablet) {
            width: 735px;
        }
    }
    &__wrapper {
        display: flex;
        flex-direction: column;
        height: 450px;
        overflow: hidden;
    }
    &__bottom {
        display: flex;
        flex-direction: row;
        height: 70px;
    }
    &__description {
        margin: auto 0;
        margin-right: 50px;
        line-height: 133%;
        @media screen and (max-width: $screen-tablet) {
            margin: 7px 20px;
        }
    }
    &__arrow-buttons {
        display: flex;
        flex-direction: row;
    }
    & button {
        width: 70px;
        height: 70px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: 21px auto;
        border: solid 1px $main-color;
        margin: 0 1px;
    }
    &__button-left {
        background-image: url('../img/svg/left-arrow.svg');
    }
    &__button-right {
        background-image: url('../img/svg/right-arrow.svg');
        margin-right: 0 !important;
    }
    &__right-block {
        display: flex;
        @media screen and (max-width: $screen-tablet) {
            min-width: 0;
        }
    }
    &__item {
        padding: 18px 130px 18px 29px;
        width: 330px;
        font-family: $h4-desctop-font;
        font-size: $h4-desctop-size;
        line-height: 130%;
        transition: .2s;
        &_active {
            background-color: $main-color;
            color: $white-color;
            border: 1px solid $main-color;
        }
        &:hover {
            background-color: $hover-color;
            color: $white-color;
            transition: .2s;
        }
        &:active {
            background-color: $active-color;
            color: $white-color;
            transition: .2s;
        }
        &::after {
            content:"\A";
            border-bottom: 1px solid $main-color;
            width: 298px;
            height: 1px;
            display: flex;
            position: absolute;
            margin: 18px -13px;
        }
        @media screen and (max-width: $screen-tablet) {
            width: 265px;
            padding: 13px 75px 13px 24px;
            &::after {
                width: 239px;
                margin: 13px -11px;
            }
        }
    }

    &__filter {
        display: flex;
        position: absolute;
        width: 960px;
        height: 450px;
        background: linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 24.48%);
        z-index: 2;
        @media screen and (max-width: $screen-tablet) {
            width: 735px;
        }
    }
    &__scroll {
        //заглушка
        display: flex;
        width: auto;
        height: 450px;
        
        background: url(../img/gallery/02.jpeg) no-repeat center;
        background-size: cover;
        z-index: 1;
    }
}

.section-gallery-mobile{
    display: none;
    @media screen and (max-width: $screen-phone) {
        display: flex;
    }
}

.number-holder {
    display: flex;
    flex-direction: column;
    padding: 41px 0;
    &__line {
        min-height: 137px;
        margin: 16px 31px 16px;
        border-left: 1px solid #FFFFFF;
    }
    &__number-list {
        min-height: 17px;
        margin: 7px 20px;
        display: flex;
        font-family: 'Gotu';
        font-size: 20px;
        color: $white-color;
    }
    &__number {
        position: absolute;
        opacity: 0;
        &_active {
