    margin-top: calc(100vw * 0.0576923 - 1400px * 0.0576923 + 209px);
    width: 100%;
    @media screen and (max-width: $screen-tablet) {
        margin-top: calc(100vw * 0.03759398 - 1000px * 0.03759398 + 153px)
    }
    @media screen and (max-width: $screen-phone) {
        margin-top: 94px;
    }
    &__title {
        margin-bottom: 59px;
        width: auto;
        text-align: end;
        margin-right: 2px;
        &::after {
            display: flex;
            content:"\A";
            width: 189px;
            border-bottom: 2px solid $main-color;
            height: 1px;
            margin: -2px -25px;
            display: block;
            margin-left: auto;
        }
        @media screen and (max-width: $screen-tablet) {
            margin-right: 50px;
            margin-bottom: 62px;
            &::after {
                width: 175px;
                margin: -2px -29px;
                margin-left: auto;
            }
        }
        @media screen and (max-width: $screen-phone) {
            margin-right: 22px;
            margin-bottom: 57px;
            &::after {
                width: 132px;
                margin: -2px -20px;
                margin-left: auto;
            }
        }
    }
    &__container {
        display: flex;
        margin: 0 -15px;
        @media screen and (max-width: $screen-tablet) {
            margin: 0 10px;
        }
        @media screen and (max-width: $screen-phone) {
            flex-direction: column;
            margin: 0
        }
    }
    &__item {
        flex-basis: auto;
        width:calc( ( 100% - 20px ) / 4 );
