        }
    }
}
.how-item {
    &__up {
        display: flex;
        justify-content: space-between;
        margin-bottom: 10px;
        border-bottom: 2px solid $main-color;
        @media screen and (max-width: $screen-phone) {
            flex-direction: row-reverse;
            justify-content: left;
            min-height: 56px;
        }
    }
    &__title {
        font-family: 'Federo';
        font-size: 28px;
        line-height: 130%;
        padding-bottom: 9px;
        color: #111111;
        @media screen and (max-width: $screen-tablet) {
            padding-top: 14px;
            font-size: 20px;
        }
        @media screen and (max-width: $screen-phone) {
            margin-top: 3px;
