        }
    }
    &__number {
        width: 39px;
        font-family: 'Gotu';
        text-align: center;
        font-weight: 400;
        font-size: 30px;
        line-height: 130%;
        padding: 10px 11px 0 8px;
        color: $white-color;
        background-color: $main-color;
        @media screen and (max-width: $screen-phone) {
            height: 56px;
        }
    }
    &__down {
        font-family: 'Century Gothic';
        font-size: 17px;
        line-height: 130%;
        color: #111111;
        p {
            margin-bottom: 14px;
            a {
                color: #0017E1;
            }
        }
        @media screen and (max-width: $screen-tablet) {
            font-size: 16px;
            margin-top: 9px;
        }
        @media screen and (max-width: $screen-phone) {
            margin: 4px 19px 28px;
            p {
                margin-bottom: 11px;
                a {
                    color: #0017E1;
                }
            }
        }
    }
}
