.position-absolute {
    display: flex;
    position: absolute;
    min-width: 440px;
    justify-content: space-between;
    max-height: 80px;
    transition: max-height .5s;
    z-index: 10;
    &:hover {
        max-height: 156px;
    }
    z-index: 10;
    @media screen and (max-width: $screen-tablet) {
        min-width: 339px;
        max-height: 70px;
        &:hover {
            max-height: 136px;
        }
    }
    @media screen and (max-width: $screen-phone) {
        min-width: 440px;
        max-height: 70px;
        &:hover {
            max-height: 136px;
        }
    }
}
.phone-form {
    height: 156px;
    display: flex;
    flex-grow: 1;
    background-color: $main-color;
    justify-content: space-between;
    max-height: 80px;
    transition: max-height .5s;
    z-index: 1;
    &__privacy{
        position: absolute;
        font-family: 'Century Gothic';
        font-size: 12px;
        line-height: 130%;
        padding-left: 37px;
        color: $main-color;
        transition: margin .5s;
        z-index: -99;
        a {
            color: $main-color;
            text-decoration: underline $main-color;
        }
        @media screen and (max-width: $screen-tablet) {
            padding-left: 16px;
            font-size: 11px;
        }
    }
    &:hover {
        .button-block {
            height: 156px;
        }
        .phone-form__privacy {
            margin-top: -20px;
        }
        max-height: 156px;
    }
    @media screen and (max-width: $screen-tablet) {
        &:hover {
            max-height: 136px;
            .button-block {
                max-height: 136px;
            }
        }
    }
    @media screen and (max-width: $screen-phone) {
        &:hover {
            max-height: 136px;
            .button-block {
                max-height: 136px;
            }
        }
    }
    input {
        width: 259px;
        height: 34px;
        background-color: transparent;
        margin: 24px 36px 22px;
        padding-bottom: 10px;
        padding-left: 3px;
        font-family: 'Century Gothic';
        font-size: 18px;
        border-bottom: 2px solid $white-color;
        color: $white-color;
        &::placeholder {
            color: $white-color;
        }
        &:focus::placeholder {
            color: transparent;
        }
        @media screen and (max-width: $screen-tablet) {
            font-size: 16px;
            margin: 20px 16px 16px;
            padding-bottom: 6px;
            padding-left: 3px;
            width: 225px;
        }
        @media screen and (max-width: $screen-phone) {
            font-size: 18px;
            margin: 17px 16px 18px;
        }
    }

    @media screen and (max-width: $screen-tablet) {
        max-height: 70px;
        max-width: 339px;
        &:hover {
            .button-block {
                height: 136px;
            }
            .phone-form__privacy {
                margin-top: -20px;
            }
            max-height: 136px;
        }
    }
    @media screen and (max-width: $screen-phone) {
        max-height: 70px;
        max-width: 440px;
        &:hover {
            .button-block {
                height: 136px;
            }
            .phone-form__privacy {
                margin-top: -20px;
            }
            max-height: 136px;
        }
    }
    
    &__inputs {
        width: 259px;
        @media screen and (max-width: $screen-tablet) {
            width: 211px;
        }
    }
    &__name {
        margin: 20px 36px !important;
        @media screen and (max-width: $screen-tablet) {
            margin: 15px 16px !important;
        }
    }
    &__send {
        background: $white-color url('../img/svg/apply.svg') no-repeat center ;
        width: 80px;
        height: 80px;
        border: 2px solid $main-color;
        &:hover {
            background-color: #d49f88;
        }
        &:active {
            background-color: #7c4832;
        }
        @media screen and (max-width: $screen-tablet) {
            width: 70px;
            height: 70px;
        }
    }
}

.button-block {
    height: 80px;
