        }
    }
    &__title {
        display: flex;
        min-height: 50px;
        padding: 12px 21px;
        font-family: 'Federo';
        font-size: 26px;
        border-bottom: 1px solid $main-color;
        &::after {
            content:"\A";
            display: flex;
            flex-direction: row-reverse;
            background: url('../img/svg/triangle-down.svg') no-repeat center;
            width: 10px;
            height: 10px;
            margin: 8px 10px;
