}


.pop-menu {
    display: none;
    @media screen and (max-width: $screen-phone) {
        display: flex;
        &__hiden {
            transition: transform .5s;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            right: 0;
        }
        &__wrapper {
            transition: transform .5s;
            position: absolute;
            right: 0;
            top: 0;
            display: flex; 
            flex-direction: column;
            align-items: flex-end;
            margin: 31px 27px;
            width: 426px;
            background-color: $white-color;
            box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            nav {
                width: 426px;
            }
        }
        &__title {
            display: flex;
            justify-content: end;
            display: flex;
            height: 46px;
            width: 100%;
        }
        &__close {
            width: 46px;
            height: 46px;
            background: url('../img/svg/close.svg') center no-repeat;
            &:hover{
                background-color: transparent;
            }
            &:active{
                background-color: transparent;
            }
        }
        &__item {
            display: flex;
            justify-content: end;
            height: 46px;
            width: 100%;
            &:hover{
                background-color: $main-color;
                a {
                    color: $white-color;
                }
            }
            &:active {
                background-color: $active-color;
            }
            a {
                align-items: center;
                display: flex;
                justify-self: end;
                font-size: 18px;
                line-height: 22px;
                text-align: left;
                height: 46px;
                min-width: auto;
                padding-right: 20px;
                &:hover{
                    text-decoration: none;
                }
            }
        }
    }
}

.pop-menu_hide {
    .pop-menu {
        &__wrapper{
            transform: translate(0, -150%);
        }
        &__hiden {
            transform: translate(0, -150%);
        }
    }
}